html {
  box-sizing: border-box;
  font-family: sans-serif;
  font-size: 16px;
  background-color: #EEE;
}

body {
  overflow-x: hidden;
}

*,
*:after,
*:before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

a {
  color: #004B76;
  cursor: pointer;
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}

a:hover {
  color: #61DAFB;
}

img {
  max-width: 100%;
  height: auto;
}

mark {
  color: #FFF;
  background-color: #CC7A6F;
}
 .Main-container {
   padding: 1rem;
 }

 .pk-logo {
   display: block;
   margin: auto;
   width: auto;
   max-height: 3rem;
 }

 .custom-menu-wrapper {
   background-color: #808080;
   margin-bottom: 2.5em;
   white-space: nowrap;
   position: relative;
 }

 .custom-menu {
   display: inline-block;
   width: auto;
   vertical-align: middle;
   -webkit-font-smoothing: antialiased;
 }

 .custom-menu .pure-menu-link,
 .custom-menu .pure-menu-heading {
   color: white;
 }

 .custom-menu .pure-menu-link:hover,
 .custom-menu .pure-menu-heading:hover {
   background-color: transparent;
 }

 .custom-menu-top {
   position: relative;
   padding-top: .5em;
   padding-bottom: .5em;
 }

 .custom-menu-brand {
   display: block;
   text-align: center;
   position: relative;
 }

 .custom-menu-toggle {
   width: 44px;
   height: 44px;
   display: block;
   position: absolute;
   top: 3px;
   right: 0;
   display: none;
 }

 .custom-menu-toggle .bar {
   background-color: white;
   display: block;
   width: 20px;
   height: 2px;
   border-radius: 100px;
   position: absolute;
   top: 22px;
   right: 12px;
   -webkit-transition: all 0.5s;
   transition: all 0.5s;
 }

 .custom-menu-toggle .bar:first-child {
   -webkit-transform: translateY(-6px);
   transform: translateY(-6px);
 }

 .custom-menu-toggle.x .bar {
   -webkit-transform: rotate(45deg);
   transform: rotate(45deg);
 }

 .custom-menu-toggle.x .bar:first-child {
   -webkit-transform: rotate(-45deg);
   transform: rotate(-45deg);
 }

 .custom-menu-screen {
   background-color: rgba(0, 0, 0, 0.5);
   -webkit-transition: all 0.5s;
   transition: all 0.5s;
   height: 3em;
   width: 70em;
   position: absolute;
   top: 0;
   z-index: -1;
 }

 .custom-menu-tucked .custom-menu-screen {
   -webkit-transform: translateY(-44px);
   transform: translateY(-44px);
 }

 @media (max-width: 64em) {

   .custom-menu {
     display: block;
   }

   .custom-menu-toggle {
     display: block;
     display: none\9;
   }

   .custom-menu-bottom {
     position: absolute;
     width: 100%;
     border-top: 1px solid #eee;
     background-color: #808080;
     z-index: 100;
   }

   .custom-menu-bottom .pure-menu-link {
     opacity: 1;
     -webkit-transform: translateX(0);
     transform: translateX(0);
     -webkit-transition: all 0.5s;
     transition: all 0.5s;
   }

   .custom-menu-bottom.custom-menu-tucked .pure-menu-link {
     -webkit-transform: translateX(-140px);
     transform: translateX(-140px);
     opacity: 0;
     opacity: 1\9;
   }

   .pure-menu-horizontal.custom-menu-tucked {
     z-index: -1;
     top: 45px;
     position: absolute;
     overflow: hidden;
   }

 }

.AuthForm {
  max-width: 80vw;
  margin: auto;
}

.AuthForm input {
  display: block;
  width: 100%;
}

.u-error {
  padding: 1rem;
  font-weight: bold;
  color: #FFF;
  background-color: #CC7A6F;
}

@media screen and (min-width: 64em) {
  .AuthForm {
    max-width: 50vw;
  }
}
.AddForm {
  max-width: 80vw;
  margin: auto;
}

.AddForm input,
.AddForm select,
.react-datepicker__input-container {
  display: block;
  width: 100%;
}

@media screen and (min-width: 64em) {
  .AddForm {
    max-width: 50vw;
  }
}
.CoursesList-item {
  margin: 1rem auto 0;
  list-style-type: none;
  background-color: #FFF;
  border-radius: .5rem;
  box-shadow: .1rem .1rem .5rem rgba(0, 0, 0, .25);
}

.CoursesList-link img {
  border-radius: .5rem .5rem 0 0;
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}

.CoursesList-link:hover img {
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
}

.CoursesList-link h2 {
  padding: 1rem;
  font-size: 5vw;
}

.CoursesList-item p {
  padding: 1rem;
}

.CoursesList-item .fa {
  padding-right: 1vw;
}

.CoursesList-item b {
  padding-right: 5vw;
}

@media screen and (min-width: 48em) {
  .CoursesList {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .CoursesList-item {
    width: 40%;
  }

  .CoursesList-item h2 {
    font-size: 1.25rem;
  }
}

@media screen and (min-width: 64em) {
  .CoursesList {
    margin: auto;
    max-width: 1200px;
  }

  .CoursesList-item {
    width: 30%;
  }
}
.SearchForm {
  margin: 1rem auto;
  max-width: 80vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.SearchForm input {
  display: block;
  width: 100%;
}

@media screen and (min-width: 64em) {
  .SearchForm {
    max-width: 50vw;
  }
}
